import React from "react";
import ToDoItems from "./ToDoItems";

const ToDos = (props) => {
  let mystyle = {
    minHeight: "70vh",
  };
  return (
    <div className="conatiner my-5" style={mystyle}>
      <h3 className="text-center my-3"> To-Do List </h3>

      {props.todos.length === 0 ? (
        <p className="text-center"> To-Do List is now Empty</p>
      ) : (
        //else

        props.todos.map((todo) => {
          return (
            <ToDoItems todo={todo} key={todo.sno} onDelete={props.onDelete} />
          );
        })
      )}
    </div>
  );
};

export default ToDos;
